.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

button {
  padding: 10px;
  cursor: pointer;
  color: darkblue;
  font-size: 1.1rem;
  border-radius: 5px;
}

.road {
  height: 60px;
  width: 100vw;
  position: relative;
  background-color: grey;
  margin-top: 2rem;
}

.auto-icon {
  font-size: 40px;
  position: absolute;
  left: 5px;
  top: 5px;
  color: brown;
}

.runAuto {
  animation: go 20s ease-in;
}

@keyframes go {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(100vw);
  }
}
